@import "mixins";
@import "./variables.scss";

html {
  overflow: hidden;
  @include onMobile {
    min-height: 80vh;
  }
}

.best-score {
  display: flex;
  flex-direction: column;
}

body {
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  background: var(--main-background-color);
  font-family: sans-serif;
  font-size: 24px;
  font-weight: 900;
  color: var(--text-color);

  @include onMobile {
    height: 80vh;
    overflow: hidden;
    touch-action: none;
  }
}

.field-cell {
  background: var(--secondary-background-color);
  width: 75px;
  height: 75px;
  border-radius: 5px;
  box-sizing: border-box;
  text-align: center;
  vertical-align: center;
  user-select: none;
}

.game-header {
  display: flex;
  width: 100%;
  gap: 5px;
  margin-bottom: 24px;
  padding: 5px;
  box-sizing: border-box;
}

.game-name {
  background: var(--accent-background-color);
  width: 80px;
  height: 80px;
  font-size: 24px;
  border-radius: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
  margin: 0;
}

.score-info {
  display: flex;
  flex-grow: 1;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border: var(--accent-background-color) 3px solid;
  height: 80px;
  border-radius: 5px;
  box-sizing: border-box;
  font-size: 18px;
}

.score {
  color: var(--accent-background-color);
}

.button {
  display: flex;
  align-items: center;
  color: var(--text-color);
  justify-content: center;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-family: sans-serif;
  font-weight: 700;
  font-size: 20px;
  width: 80px;
  height: 80px;
  transition: 0.25s ease all;
}

.start {
  background: var(--button-color);

  &:hover {
    background: var(--button-hover-color);
  }
}

.restart {
  background: var(--accent-background-color);

  &:hover {
    opacity: 0.8;
    transition: all 0.2s ease;
  }
}

.message {
  box-sizing: border-box;
  width: 100%;
  padding: 10px;
  text-align: center;
  border-radius: 5px;
  font-size: 20px;
}

.hidden {
  display: none;
}

.container-main {
  display: flex;
  width: 80%;
  align-items: start;
  justify-content: space-around;
}

.container {
  display: flex;
  flex-direction: column;
  width: 350px;
  text-align: center;
  align-items: center;
  box-shadow: var(--box-shadow);
  border-radius: 10px;
  padding-left: 20px;
  padding-right: 20px;
  padding-top: 20px;
  background: var(--secondary-background-color);
}

.game-field {
  position: relative;
  background: var(--text-color);
  border-spacing: 10px;
  border-radius: 5px;
}

.message-start {
  background: var(--button-color);
}

.message-lose {
  background: var(--message-lose-bg);
}

.message-win {
  background: var(--message-win-bg);
}

.message-container {
  width: 100%;
  height: 150px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.tile {
  background: #d6cdc4;
  width: 75px;
  height: 75px;
  border-radius: 5px;
  box-sizing: border-box;
  text-align: center;
  vertical-align: center;
  user-select: none;
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 0.3s ease;
  animation: scaleTile 0.3s backwards ease-in-out 0.3s;

  @keyframes scaleTile {
    0% {
      scale: 0;
    }
    50% {
      scale: 1.2;
    }
    100% {
      scale: 1;
    }
  }

  &--2 {
    background: var(--tile-color-2);
  }

  &--4 {
    background: var(--tile-color-4);
  }

  &--8 {
    background: var(--tile-color-8);
  }

  &--16 {
    background: var(--tile-color-16);
  }

  &--32 {
    background: var(--tile-color-32);
  }

  &--64 {
    background: var(--tile-color-64);
  }

  &--128 {
    background: var(--tile-color-128);
  }

  &--256 {
    background: var(--tile-color-256);
  }

  &--512 {
    background: var(--tile-color-512);
  }

  &--1024 {
    background: var(--tile-color-1024);
  }

  &--2048 {
    background: var(--tile-color-2048);
  }
}

.theme-options {
  display: flex;
  gap: 12px;
}

.theme-options label {
  cursor: pointer;
  font-size: 14px;
  font-weight: bold;
}

.theme-options input[type="radio"] {
  appearance: none;
  width: 16px;
  height: 16px;
  margin-right: 4px;
  border: 2px solid var(--secondary-background-color);
  border-radius: 50%;
  cursor: pointer;
  position: relative;
}

.theme-options input[type="radio"]:checked {
  background-color: var(--accent-background-color);
  border-color: var(--accent-background-color);
}

.change-theme {
  display: flex;
  flex-direction: column;
}
